.iframe-container {
    position: relative;
    width: 100%;
    padding-bottom: 100.25%; /* Set the aspect ratio (16:9) based on your iframe content */
    overflow: hidden;
  }
  
  .iframe-container iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  